"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

const serviceAreas = [
  "Rawat Jalan",
  "Rawat Inap",
  "Penunjang",
  "Lainnya"
];

const serviceTypes = {
  "Rawat Jalan": ["Poli Umum", "Poli Gigi", "Poli Anak", "Poli Mata"],
  "Rawat Inap": ["Kelas 1", "Kelas 2", "Kelas 3", "VIP"],
  "Penunjang": ["Laboratorium", "Radiologi", "Farmasi", "Rehabilitasi"],
  "Lainnya": ["Administrasi", "Pendaftaran", "Kasir"]
};

export default function Home() {
  const [formData, setFormData] = useState({
    fullname: "",
    nik: "",
    serviceArea: "",
    serviceType: ""
  });

  const [step, setStep] = useState(1);
  const [rating, setRating] = useState(0);
  const [timer, setTimer] = useState(300); // 5 minutes in seconds

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleSubmit = async () => {
    try {
      const response = await fetch("/api/survey/submit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          ...formData,
          rating
        }),
      });

      if (response.ok) {
        setStep(3); // Show thank you message
      }
    } catch (error) {
      console.error("Error submitting survey:", error);
    }
  };

  return (
    <div className="max-w-2xl mx-auto">
      <Card className="p-6 shadow-lg bg-white">
        <div className="text-center mb-6">
          <h1 className="text-2xl font-bold text-gray-900 mb-2">
            Survei Kepuasan Pasien
          </h1>
          <p className="text-gray-600">
            RSUD dr Mohamad Soewandhie
          </p>
        </div>

        {step === 1 && (
          <div className="space-y-4">
            <div>
              <Label htmlFor="fullname">Nama Lengkap</Label>
              <Input
                id="fullname"
                value={formData.fullname}
                onChange={(e) => handleInputChange("fullname", e.target.value)}
                placeholder="Masukkan nama lengkap"
                className="mt-1"
              />
            </div>

            <div>
              <Label htmlFor="nik">NIK</Label>
              <Input
                id="nik"
                value={formData.nik}
                onChange={(e) => handleInputChange("nik", e.target.value)}
                placeholder="Masukkan NIK"
                className="mt-1"
              />
            </div>

            <div>
              <Label>Bidang/Area Pelayanan</Label>
              <Select
                value={formData.serviceArea}
                onValueChange={(value) => {
                  handleInputChange("serviceArea", value);
                  handleInputChange("serviceType", "");
                }}
              >
                <SelectTrigger className="mt-1">
                  <SelectValue placeholder="Pilih area pelayanan" />
                </SelectTrigger>
                <SelectContent>
                  {serviceAreas.map((area) => (
                    <SelectItem key={area} value={area}>
                      {area}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {formData.serviceArea && (
              <div>
                <Label>Jenis Pelayanan</Label>
                <Select
                  value={formData.serviceType}
                  onValueChange={(value) => handleInputChange("serviceType", value)}
                >
                  <SelectTrigger className="mt-1">
                    <SelectValue placeholder="Pilih jenis pelayanan" />
                  </SelectTrigger>
                  <SelectContent>
                    {serviceTypes[formData.serviceArea as keyof typeof serviceTypes].map((type) => (
                      <SelectItem key={type} value={type}>
                        {type}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            )}

            <Button
              className="w-full mt-6"
              onClick={() => setStep(2)}
              disabled={!formData.fullname || !formData.nik || !formData.serviceArea || !formData.serviceType}
            >
              Lanjutkan
            </Button>
          </div>
        )}

        {step === 2 && (
          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-xl font-semibold mb-4">
                Bagaimana pelayanan kami di {formData.serviceType}?
              </h2>
              <div className="flex justify-center space-x-4">
                {[1, 2, 3, 4, 5].map((star) => (
                  <button
                    key={star}
                    onClick={() => setRating(star)}
                    className={`text-4xl ${
                      rating >= star ? "text-yellow-400" : "text-gray-300"
                    }`}
                  >
                    â˜…
                  </button>
                ))}
              </div>
              <p className="mt-2 text-sm text-gray-600">
                Klik bintang untuk memberikan penilaian
              </p>
            </div>

            <div className="text-center">
              <p className="text-sm text-gray-600 mb-4">
                Sisa waktu: {Math.floor(timer / 60)}:{(timer % 60).toString().padStart(2, "0")}
              </p>
              <Button
                className="w-full"
                onClick={handleSubmit}
                disabled={!rating}
              >
                Kirim Penilaian
              </Button>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="text-center space-y-4">
            <h2 className="text-2xl font-semibold text-green-600">
              Terima Kasih!
            </h2>
            <p className="text-gray-600">
              Penilaian Anda telah kami terima.
            </p>
            <Button
              className="w-full"
              onClick={() => {
                setStep(1);
                setFormData({
                  fullname: "",
                  nik: "",
                  serviceArea: "",
                  serviceType: ""
                });
                setRating(0);
                setTimer(300);
              }}
            >
              Isi Survei Baru
            </Button>
          </div>
        )}
      </Card>
    </div>
  );
}
